@import "@/style/common/common.scss";


















































































































































































































.component-market{    
    height: calc(100% - 50px);
    overflow: hidden;
    .market_search{
        height: 105px;
        .group_box{
            padding: 15px 15px 10px 15px;
            >div{
                display: block;
            }
        }
        .search_box{
            padding:0px 15px;
            display: flex;
        }
        .list_type_btn{
            margin-left: 10px;
        }
    }
    .market_content{
        height: calc(100% - 105px);
        overflow-x: hidden;
        overflow-y: auto;
        .market_content_inner{
            min-height: 20px;
            .no_market_content{
                text-align: center;
                padding: 15px 0px;
                opacity: 0.6;
                height: initial !important;
                margin: 0px auto;
            }
            //
            &.gridmarket{
                display: flex;
                flex-wrap:wrap;
                >div:not(.no_market_content){
                }
            }
        }
    }
    .ant-divider-horizontal{
        margin: 0px 15px;
    }
}
