@import "@/style/common/common.scss";















































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































































.dragarea-top-right-action {
  display: flex;
  justify-content: flex-end;
  .backout {
    transform: rotateY(180deg);
  }
  & > div {
    padding: 0px 8px;
    white-space: nowrap;
    cursor: pointer;
    > a {
      display: block;
    }
    &[disabled] {
      opacity: 0.3;
      cursor: not-allowed;
      a {
        cursor: not-allowed;
      }
      .savebtn {
        &:hover {
          opacity: 1;
        }
      }
    }
    svg {
      outline: none;
    }
  }
  .oneicon {
    font-size: 16px;
  }
  .savebtn {
    padding: 0px;
    margin-left: 8px;
    width: 80px;
    text-align: center;
    font-size: 16px;
    // background-color: #1890ff;
    // color: #ffffff;
    transition: all linear 0.3s;
    &:hover {
      opacity: 0.8;
    }
  }
}

//
.menu-option-item {
  padding-left: 20px;
  padding-right: 120px;
  display: inline-block;
  position: relative;
  width: 100%;
  .anticon {
    display: none;
    position: absolute;
    left: 0;
    top: 3px;
  }
  &.active {
    .anticon {
      display: inline-block;
    }
  }
  .shortcutkey {
    width: 100px;
    display: inline-block;
    position: absolute;
    right: 10px;
    text-align: right;
    opacity: 0.4;
  }
  &[disabled] {
    cursor: not-allowed;
    opacity: 0.3;
  }
}
