//
@each $themename,$theme in $platformDevTheme {
    .develop_theme-#{$themename}{
        .idmcore_develop{
            
        .drag_simulator_overlay{
            .simulator_outline{
                &.simulator_dropping_outline{
                    outline-color: map-get($map: $theme, $key: "mainColor");
                }
                &.simulator_selected_outline{
                    outline-color: map-get($map: $theme, $key: "mainColor");
                }
                &.simulator_hovering_outline{
                    outline-color: map-get($map: $theme, $key: "mainColor");
                    background-color: map-get($map: $theme, $key: "lowAlphaColor");
                }
                .simulator_outline_node_selector{
                    background: map-get($map: $theme, $key: "mainColor");
                }
                .simulator_outline_actions{
                    &>div{
                        background: map-get($map: $theme, $key: "mainColor");
                    }
                }
            }
            .simulator_insertion{
                background-color:map-get($map: $theme, $key: "mainColor");
            }
            .simulator_insertion:after,
            .simulator_insertion:before{
                border-color:transparent transparent transparent map-get($map: $theme, $key: "mainColor")
            }
            .simulator_insertion:after{
                border-color:transparent map-get($map: $theme, $key: "mainColor") transparent transparent
            }
            .simulator_insertion.simulator_vertical:before{
                border-color:map-get($map: $theme, $key: "mainColor") transparent transparent
            }
            .simulator_insertion.simulator_vertical:after{
                border-color:transparent transparent map-get($map: $theme, $key: "mainColor")
            }
        }
        background-color: map-get($map: $theme, $key: "bgcolor");
        .rul_corner,.rul_ruler{
            background-color: map-get($map: $theme, $key: "qBgcolor");
        }
        .rul_corner:hover{
            background-color: map-get($map: $theme, $key: "bgcolor");
        }
        .rul_line{
            border-bottom-color: map-get($map: $theme, $key: "guidesColor");
            border-left-color: map-get($map: $theme, $key: "guidesColor");
            &.rul_line_dragged{
                // border-bottom: 1px dotted #ca0828;
                border-color: map-get($map: $theme, $key: "guidesColorD") !important;
            }
        }
        .rul_line:hover{
            border-bottom-color: map-get($map: $theme, $key: "guidesColorH");
            border-left-color: map-get($map: $theme, $key: "guidesColorH");
        }
        .top_toolbar{
            background-color: map-get($map: $theme, $key: "qBgcolor");
            box-shadow: 0px 3px 5px map-get($map: $theme, $key: "shadowColor");
            .ctrl_btn{
            background-color: map-get($map: $theme, $key: "transparentColor");
            }
        }
        .bottom_action_panel{
            background-color: map-get($map: $theme, $key: "qBgcolor");
            box-shadow: 0px -3px 5px map-get($map: $theme, $key: "shadowColor");
            .idm_bottom_action_panel_bottom_action{
              box-shadow: 0px -3px 5px map-get($map: $theme, $key: "shadowColor");
            }
            .dragarea-bottom-history{
                .history_date{
                  color: map-get($map: $theme, $key: "mainFontColor");
                }
                .history_action{
                  color: map-get($map: $theme, $key: "minorFontColor");
                }
                .history_content{
                  color: map-get($map: $theme, $key: "mainFontColor");
                }
                .history_info{
                  color: map-get($map: $theme, $key: "lowFontColor");
                }
                .ant-timeline-item-tail{
                  border-left-color: map-get($map: $theme, $key: "lowBorderColor");
                }
            }
        }
        .lr_sidebar{
            background-color: map-get($map: $theme, $key: "qBgcolor");
            box-shadow: 3px 5px 5px map-get($map: $theme, $key: "shadowColor");
            .ctrl_btn{
            background-color: map-get($map: $theme, $key: "transparentColor");
            }
            &.pos_right{
            box-shadow: -3px 5px 5px map-get($map: $theme, $key: "shadowColor");
            }
            .ant-tabs{
            &.ant-tabs-bottom .ant-tabs-bottom-bar{
                background-color: map-get($map: $theme, $key: "bgcolor");
            color: map-get($map: $theme, $key: "minorFontColor");
            }
            }
            .pane_content_title{
            background-color: map-get($map: $theme, $key: "bgcolor");
            color: map-get($map: $theme, $key: "minorFontColor");
            }
        }
        //
        .drag_workspace{
            background-color: map-get($map: $theme, $key: "workspaceBgcolor");
            box-shadow: 0px 0px 10px map-get($map: $theme, $key: "shadowColor");
        }
        //
        .mobile_status_bar{
          color: map-get($map: $theme, $key: "mainFontColor");
        }
        
        //
        .dragarea-top-left-title{
            >div.backbtn{
                color: map-get($map: $theme, $key: "minorFontColor");
            }
            .title-content{
                color: map-get($map: $theme, $key: "minorFontColor");
                .readonlytitle{
                    div{
                    }
                }
            }
        }
        //
        .dragarea-top-right-action{
            &>div{
            a,i,.oneicon{
                color: map-get($map: $theme, $key: "lowFontColor");
            }
            &:hover{
                a,i,.oneicon{
                color: map-get($map: $theme, $key: "mainFontColor");
                }
            }
            &[disabled]{
                a,i,.oneicon{
                    color: map-get($map: $theme, $key: "lowFontColor");
                }
            }
            .savebtn{
                color: map-get($map: $theme, $key: "qBgcolor");
                background-color: map-get($map: $theme, $key: "mainColor");
            }
            }
        }
        //
        .drag-area-size-droplist{
            .symbol-multiply-by{
                color: map-get($map: $theme, $key: "lowFontColor");
            }
        }
        }
        
        .scrollbar_style{
            &::-webkit-scrollbar-thumb {
                background-color: map-get($map: $theme, $key: "scrollBarColor");
            }
        }
        //
        .idm-quick-search{
          color: map-get($map: $theme, $key: "qBgcolor");
          background-color: map-get($map: $theme, $key: "mainColor");
          box-shadow: 0px 0px 15px map-get($map: $theme, $key: "showyShadowColor");
          .idm-quick-search-input{
              input{
                  &::placeholder{
                    color: rgba(map-get($map: $theme, $key: "qBgcolor"),0.35);
                  }
              }
          }
          .idm-quick-search-close{
            color: map-get($map: $theme, $key: "qBgcolor");
          }
          .idm-quick-search-results{
              .idm-quick-search-result-item-wrapper{
                  .idm-quick-search-type-node{
                      background: rgba(map-get($map: $theme, $key: "qBgcolor"),0.3);
                  }
                  .idm-quick-search-result-item-title-wrapper{
                      .idm-quick-search-result-item-name{
                        color: map-get($map: $theme, $key: "qBgcolor");
                      }
                      .idm-quick-search-result-item-version{
                        color: rgba(map-get($map: $theme, $key: "qBgcolor"),0.3);
                      }
                  }
                  .idm-quick-search-result-item-content{
                      color: rgba(map-get($map: $theme, $key: "qBgcolor"),0.6);
                  }
                  &:hover{
                      background: rgba(map-get($map: $theme, $key: "qBgcolor"),0.1);
                  }
              }
              
          }
          .idm-svg-icon{
            color: map-get($map: $theme, $key: "qBgcolor");
          }
        }
          //
          .idm-quick-search-popover.min-search{
            color: map-get($map: $theme, $key: "mainFontColor");
            background-color: map-get($map: $theme, $key: "qBgcolor");
            .idm-quick-search-input{
                input{
                  border: 1px solid map-get($map: $theme, $key: "borderColor");
                    &::placeholder{
                      color: map-get($map: $theme, $key: "lowFontColor");
                    }
                }
            }
            .idm-quick-search-results{
                .idm-quick-search-result-item-wrapper{
                    .idm-quick-search-type-node{
                        background: rgba(map-get($map: $theme, $key: "pureColor"),0.1);
                        .idm-svg-icon{
                          color: map-get($map: $theme, $key: "lowFontColor");
                        }
                    }
                    .idm-quick-search-result-item-title-wrapper{
                        .idm-quick-search-result-item-name{
                          color: map-get($map: $theme, $key: "mainFontColor");
                        }
                        .idm-quick-search-result-item-version{
                          color: rgba(map-get($map: $theme, $key: "pureColor"),0.3);
                        }
                    }
                    .idm-quick-search-result-item-content{
                        color: rgba(map-get($map: $theme, $key: "pureColor"),0.6);
                    }
                    &:hover{
                        background: rgba(map-get($map: $theme, $key: "pureColor"),0.1);
                    }
                    .check_layer{
                        background-color: rgba(map-get($map: $theme, $key: "qBgcolor"),0.7);
                        color: map-get($map: $theme, $key: "mainColor");
                    }
                }
                &.idm-attr-item-box{
                  .idm-attr-item-wrapper{
                    &:hover{
                        background: rgba(map-get($map: $theme, $key: "qBgcolor"),0.1);
                    }
                    .check_layer{
                      background-color: rgba(map-get($map: $theme, $key: "qBgcolor"),0.7);
                      border: 2px solid map-get($map: $theme, $key: "mainColor");
                      color: map-get($map: $theme, $key: "mainColor");
                    }
                  }
                }
            }
          }
        //market no data
        .no_market_content{
            color: map-get($map: $theme, $key: "lowFontColor");
        }
        //
        .drag_container_empty{
            &:before{
                color: map-get($map: $theme, $key: "lowFontColor");
            }
            background: map-get($map: $theme, $key: "bgcolor");
            color: map-get($map: $theme, $key: "lowFontColor");
            outline-color: rgba(map-get($map: $theme, $key: "pureColor"),0.35);
        }
        .page-component{
            .pagecom_navigator{
                .pagecom_navigator_inner{
                    color: rgba(map-get($map: $theme, $key: "pureColor"),0.55);
                    .right_arrow{
                        color: rgba(map-get($map: $theme, $key: "pureColor"),0.15);
                    }
                }
            }
        }
        //
        .mix-attribute{
            .mixattr_baseinfo{
                color: map-get($map: $theme, $key: "mainFontColor");
              .mixattr_baseinfo_version{
                color: map-get($map: $theme, $key: "lowFontColor");
              }
            }
        }
        //
        .mix-attribute,.data-attribute{
            .collapse_level_0 .ant-collapse-item .ant-collapse-content{
                background: rgba(map-get($map: $theme, $key: "pureColor"),0.03);
                
                .collapse_level_1 .ant-collapse-item .ant-collapse-content{
                    background: rgba(map-get($map: $theme, $key: "pureColor"),0.03);
                    
                    .collapse_level_2 .ant-collapse-item .ant-collapse-content{
                        background: rgba(map-get($map: $theme, $key: "pureColor"),0.03);
                    }
                }
            }
            .label_text{
                color: map-get($map: $theme, $key: "minorFontColor");
            }
            //padding margin 
            .ibox_layout_padding_margin{
              .ibox_layout_handle{
                  &.ibox_handle_top{
                      border-top-color: rgba(map-get($map: $theme, $key: "pureColor"),0.3);
                  }
                  &.ibox_handle_bottom{
                      border-bottom-color: rgba(map-get($map: $theme, $key: "pureColor"),0.3);
                  }
                  &.ibox_handle_left{
                      border-left-color: rgba(map-get($map: $theme, $key: "pureColor"),0.3);
                  }
                  &.ibox_handle_right{
                      border-right-color: rgba(map-get($map: $theme, $key: "pureColor"),0.3);
                  }
              }
              .ibox_inline_input{
                  .ibox_inline_input_text{
                      color: rgba(map-get($map: $theme, $key: "pureColor"),0.6);
                  }
                  .ibox_inline_input_input{
                      color: rgba(map-get($map: $theme, $key: "pureColor"),0.6);
                      background: rgba(map-get($map: $theme, $key: "pureColor"),0.5);
                      &::placeholder{
                        color: rgba(map-get($map: $theme, $key: "pureColor"),0.2);
                      }
                  }
                  &.ibox_highlight{
                      .ibox_inline_input_text{
                          color: map-get($map: $theme, $key: "mainColor");
                      }
                  }
              }
              .box_auto_btn_box{
                  color: rgba(map-get($map: $theme, $key: "pureColor"),0.3);
                  &:hover{
                      color: rgba(map-get($map: $theme, $key: "pureColor"),0.8);
                  }
                  &.active{
                      background-color: rgba(map-get($map: $theme, $key: "pureColor"),0.1);
                  }
              }
          }
          .ctrl_box_border,.ctrl_box_radius{
              border: 1px solid map-get($map: $theme, $key: "lowBorderColor");
              .border_ctrl_wrap{
                  .border_setting_row{
                      &>div{
                          color: rgba(map-get($map: $theme, $key: "pureColor"),0.6);
                          &:hover{
                              color: rgba(map-get($map: $theme, $key: "pureColor"),0.8);
                          }
                          &.active{
                              background-color: rgba(map-get($map: $theme, $key: "pureColor"),0.1);
                          }
                      }
                  }
              }
          }
          .ctrl_box{
              .idm-svg-icon{
                color: map-get($map: $theme, $key: "mainColor");
              }
              .action_item{
                color: map-get($map: $theme, $key: "minorFontColor");
                &:hover{
                  background-color: map-get($map: $theme, $key: "lowAlphaColor");
                }
                  .action_icon>*{
                    color: map-get($map: $theme, $key: "lowFontColor");
                  }
              }
          }
        }
        //dom
        .top_toolbar,.lr_sidebar,.bottom_action_panel,.idm_attr_actionselect{
            //ant input number
            .ant-input-number{
                background:transparent;
                border-color: map-get($map: $theme, $key: "borderColor");
                color: map-get($map: $theme, $key: "minorFontColor");
                &:hover,&:focus,&.ant-input-number-focused{
                border-color: map-get($map: $theme, $key: "minorColor");
                }
            }
            .ant-input-number:hover {
            border-color: map-get($map: $theme, $key: "minorColor");
            }
            .ant-input-number:focus,.ant-input-number-focused {
            border-color: map-get($map: $theme, $key: "minorColor");
            box-shadow: 0 0 0 2px map-get($map: $theme, $key: "mainShadowColor");
            }
            .ant-input-number-handler-up-inner, .ant-input-number-handler-down-inner,.ant-input-search-icon {
                color: map-get($map: $theme, $key: "lowFontColor");
            }
            .ant-input-number-handler:hover .ant-input-number-handler-up-inner,
            .ant-input-number-handler:hover .ant-input-number-handler-down-inner {
            color: map-get($map: $theme, $key: "minorColor");
            }
            .ant-input-number-handler-wrap,.ant-input-number-handler-down{
                background:transparent;
                border-color: map-get($map: $theme, $key: "borderColor");
            }
            .ant-input-number-handler:active {
            background: map-get($map: $theme, $key: "bgcolor");
            }
            //ant input
            .ant-input-affix-wrapper,.ant-input {
            color: map-get($map: $theme, $key: "minorFontColor");
            background: transparent;
            }
            .ant-input-affix-wrapper:hover .ant-input:not(.ant-input-disabled) {
            border-color: map-get($map: $theme, $key: "minorColor");
            }
            .ant-input-affix-wrapper .ant-input-prefix,
            .ant-input-affix-wrapper .ant-input-suffix {
            color: map-get($map: $theme, $key: "lowFontColor");
            }
            .ant-input {
            border-color: map-get($map: $theme, $key: "borderColor");
            }
            .ant-input::-moz-placeholder {
            color: map-get($map: $theme, $key: "lowFontColor");
            }
            .ant-input:-ms-input-placeholder {
            color: map-get($map: $theme, $key: "lowFontColor");
            }
            .ant-input::-webkit-input-placeholder {
            color: map-get($map: $theme, $key: "lowFontColor");
            }
            .ant-input:hover {
            border-color: map-get($map: $theme, $key: "minorColor");
            }
            .ant-input:focus {
            border-color: map-get($map: $theme, $key: "minorColor");
            box-shadow: 0 0 0 2px map-get($map: $theme, $key: "mainShadowColor");
            }
            .ant-input-disabled {
            color: map-get($map: $theme, $key: "disabledFontColor");
            background-color: map-get($map: $theme, $key: "disabledBgColor");
            }
            .ant-input-disabled:hover {
            border-color: map-get($map: $theme, $key: "borderColor");
            }
            .ant-input[disabled] {
            color: map-get($map: $theme, $key: "disabledFontColor");
            background-color: map-get($map: $theme, $key: "disabledBgColor");
            }
            .ant-input::placeholder,.ant-input-affix-wrapper .ant-input-disabled ~ .ant-input-suffix .anticon{
            color: map-get($map: $theme, $key: "disabledFontColor");
            }
            .ant-input[disabled]:hover {
            border-color: map-get($map: $theme, $key: "borderColor");
            }
        }
        
        //ant message
        .ant-message {
        color: map-get($map: $theme, $key: "minorFontColor");
        }
        .ant-message-notice-content {
        background-color: map-get($map: $theme, $key: "bgcolor");
        }
        //ant collapse
        //ant
        .component-market,.page-component,.mix-attribute,.data-attribute,.bottom_action_panel,.idm_dev_modal{
          
          .dialog_check_icon{
            color: map-get($map: $theme, $key: "mainColor");
          }
          .ant-tag {
            color: rgba(map-get($map: $theme, $key: "pureColor"), 0.65);
            // background: #fafafa;
            border: 1px solid map-get($map: $theme, $key: "borderColor");
          }
          .ant-tag,
          .ant-tag a,
          .ant-tag a:hover {
            color: rgba(map-get($map: $theme, $key: "pureColor"), 0.65);
          }
          .ant-tag .anticon-close {
            color: rgba(map-get($map: $theme, $key: "pureColor"), 0.45);
          }
          .ant-tag .anticon-close:hover {
            color: rgba(map-get($map: $theme, $key: "pureColor"), 0.85);
          }
          .ant-tag-has-color,
          .ant-tag-has-color a,
          .ant-tag-has-color a:hover,
          .ant-tag-has-color .anticon-close,
          .ant-tag-has-color .anticon-close:hover {
            color: map-get($map: $theme, $key: "qBgcolor");
          }
          .ant-tag-checkable:not(.ant-tag-checkable-checked):hover {
            color: map-get($map: $theme, $key: "mainColor");
          }
          .ant-tag-checkable:active,
          .ant-tag-checkable-checked {
            color: map-get($map: $theme, $key: "qBgcolor");
          }
          .ant-tag-checkable-checked {
            background-color: map-get($map: $theme, $key: "mainColor");
          }
          .ant-tag-checkable:active {
            background-color: map-get($map: $theme, $key: "superMainColor");
          }
            .ant-collapse{
                background-color: map-get($map: $theme, $key: "qBgcolor");
            }
            .ant-collapse,.ant-collapse > .ant-collapse-item,.ant-collapse-content{
                border-color:map-get($map: $theme, $key: "lowBorderColor");
            }
            .ant-collapse > .ant-collapse-item > .ant-collapse-header{
                color: map-get($map: $theme, $key: "minorFontColor");
            }
            .ant-collapse-content{
                background-color: map-get($map: $theme, $key: "qBgcolor");
            }


            .ant-btn {
                box-shadow: 0 2px 0 rgba(59, 47, 47, 0.015);
                color: rgba(map-get($map: $theme, $key: "pureColor"),0.65);
                background-color: map-get($map: $theme, $key: "qBgcolor");
                border-color: map-get($map: $theme, $key: "borderColor");
            }
            .ant-btn:hover,
            .ant-btn:focus {
                color: map-get($map: $theme, $key: "minorColor");
                background-color: map-get($map: $theme, $key: "qBgcolor");
                border-color: map-get($map: $theme, $key: "minorColor");
            }
            .ant-btn:active,
            .ant-btn.active {
                color: map-get($map: $theme, $key: "mainColor");
                background-color: map-get($map: $theme, $key: "qBgcolor");
                border-color: map-get($map: $theme, $key: "mainColor");
            }
            .ant-btn-disabled,
            .ant-btn.disabled,
            .ant-btn[disabled],
            .ant-btn-disabled:hover,
            .ant-btn.disabled:hover,
            .ant-btn[disabled]:hover,
            .ant-btn-disabled:focus,
            .ant-btn.disabled:focus,
            .ant-btn[disabled]:focus,
            .ant-btn-disabled:active,
            .ant-btn.disabled:active,
            .ant-btn[disabled]:active,
            .ant-btn-disabled.active,
            .ant-btn.disabled.active,
            .ant-btn[disabled].active {
                color: rgba(map-get($map: $theme, $key: "pureColor"),0.25);
                background-color: rgba(map-get($map: $theme, $key: "pureColor"),0.05);
                border-color: map-get($map: $theme, $key: "borderColor");
            }
            .ant-btn:hover,
            .ant-btn:focus,
            .ant-btn:active,
            .ant-btn.active {
                background: map-get($map: $theme, $key: "qBgcolor");
            }
            .ant-btn-primary {
                color: map-get($map: $theme, $key: "qBgcolor");
                background-color: map-get($map: $theme, $key: "mainColor");
                border-color: map-get($map: $theme, $key: "mainColor");
                text-shadow: 0 -1px 0 rgba(map-get($map: $theme, $key: "pureColor"),0.12);
                box-shadow: 0 2px 0 rgba(map-get($map: $theme, $key: "pureColor"),0.045);
            }
            .ant-btn-primary:hover,
            .ant-btn-primary:focus {
                color: map-get($map: $theme, $key: "qBgcolor");
                background-color: map-get($map: $theme, $key: "minorColor");
                border-color: map-get($map: $theme, $key: "minorColor");
            }
            .ant-btn-primary:active,
            .ant-btn-primary.active {
                color: map-get($map: $theme, $key: "qBgcolor");
                background-color: map-get($map: $theme, $key: "mainColor");
                border-color: map-get($map: $theme, $key: "mainColor");
            }
            .ant-btn-primary-disabled,
            .ant-btn-primary.disabled,
            .ant-btn-primary[disabled],
            .ant-btn-primary-disabled:hover,
            .ant-btn-primary.disabled:hover,
            .ant-btn-primary[disabled]:hover,
            .ant-btn-primary-disabled:focus,
            .ant-btn-primary.disabled:focus,
            .ant-btn-primary[disabled]:focus,
            .ant-btn-primary-disabled:active,
            .ant-btn-primary.disabled:active,
            .ant-btn-primary[disabled]:active,
            .ant-btn-primary-disabled.active,
            .ant-btn-primary.disabled.active,
            .ant-btn-primary[disabled].active {
                color: rgba(map-get($map: $theme, $key: "pureColor"),0.25);
                background-color: rgba(map-get($map: $theme, $key: "pureColor"),0.05);
                border-color: map-get($map: $theme, $key: "borderColor");
            }
            .ant-btn-group .ant-btn-primary:not(:first-child):not(:last-child) {
                border-right-color: map-get($map: $theme, $key: "minorColor");
                border-left-color: map-get($map: $theme, $key: "minorColor");
