.idmcore_develop{
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    position: relative;
    transition: all linear 0.3s;
    #drag_wrapper{
        height: 100vh;
        width: 100vw;
        position: relative;
        z-index: 100;
        transition: all linear 0.3s;
        overflow: auto;
        
        .drag_workspace{
            transition: background linear 0.3s;
        }
        //
        .mobile_status_bar{
            height: 24px;
            line-height: 24px;
            display: flex;
            justify-content: space-between;
            padding: 0px 8px;
            .mobile_bar_icon{
                margin-left: 8px;
            }
        }
    }
    .drag_simulator_overlay{
        pointer-events: none;
        overflow: visible;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 60;
        .simulator_outline{
            box-sizing: border-box;
            pointer-events: none;
            position: absolute;
            &.simulator_dropping_outline{
                outline: 1px solid rgba(0,108,255,.6);
                z-index: 1;
                outline-offset: -1px;
            }
            &.simulator_selected_outline{
                outline: 2px solid rgba(0,108,255,.6);
                z-index: 1;
                outline-offset: -2px;
            }
            &.simulator_hovering_outline{
                outline: 1px dashed rgba(0,108,255,.6);
                z-index: 1;
                outline-offset: -1px;
            }
            .simulator_selected_outline_actions_wrapper{
                position: absolute;
                display: flex;
                font-size: 12px !important;
            }
            .simulator_space_top_right{
                right: 0;
                top: -22px;
            }
            .simulator_space_bottom_right{
                right: 0;
                bottom: -22px;
            }
            .simulator_outline_node_selector{
                position: relative;
                color: #fff;
                border-radius: 3px;
                // margin-right: 2px;
                pointer-events: auto;
                flex-grow: 0;
                flex-shrink: 0;
                .simulator_outline_node_selector_current{
                    padding: 0 6px;
                    display: flex;
                    align-items: center;
                    height: 20px;
                    cursor: pointer;
                    transition: opacity linear 0.2s;
                    &:hover{
                        opacity: 0.8;
                    }
                }
                .idm-svg-icon{
                    margin-right: 5px;
                    flex-grow: 0;
                    flex-shrink: 0;
                    font-size: 14px !important;
                    max-height: 14px;
                    width: 14px;
                    fill: currentColor;
                    color: white;
                }
                .node_down{
                    transition: all .3s ease;
                }
                .node_text{
                    margin-right: 8px;
                    flex-grow: 0;
                    flex-shrink: 0;
                }
                .simulator_outline_node_selector_popup{
                    position: absolute;
                    left: 0;
                    right: 0;
                    opacity: 0;
                    visibility: hidden;
                    transition: all linear 0.2s;
                    &.simulator_outline_node_selector_popup_top{
                        bottom: 20px;
                    }
                    &.simulator_outline_node_selector_popup_bottom{
                        top: 20px;
                    }
                    .simulator_outline_node_selector_popup_item{
                        margin: 2px 0;
                        transition: opacity linear 0.2s;
                        cursor: pointer;
                        &:hover{
                            opacity: 0.8;
                        }
                        .simulator_outline_node_selector_popup_item_inner{
                            background: #78869a;
                            display: inline-flex;
                            border-radius: 3px;
                            padding-left: 6px;
                            align-items: center;
                            height: 20px;
                            color: #fff;
                            cursor: pointer;
                            overflow: visible;
                        }
                        .node_text{
                            margin-right: 0;
                            line-height: 20px;
                            background: #78869a;
                            border-radius: 3px;
                            padding-right: 6px;
                        }
                    }
                }
                &:hover{
                    .simulator_outline_node_selector_popup{
                        opacity: 1;
                        visibility: visible;
                    }
                    .node_down{
                        transform: rotate(180deg);
                    }
                }
            }
            .simulator_outline_actions{
                pointer-events: auto;
                display: flex;
                &>div{
                    box-sizing: border-box;
                    cursor: pointer;
                    height: 20px;
                    width: 20px;
                    text-align: center;
                    color: #ffffff;
                    border-radius: 3px;
                    margin-left: 2px;
                    transition: opacity linear 0.2s;
                    line-height: 20px;
                    &:hover{
                        opacity: 0.8;
                    }
                }
                .idm_module_handle{
                    display: block;
                    width: 100%;
                    height: 100%;
                }
                .idm_module_handle_animate{ 
                    -webkit-animation-duration: 1s; 
                    animation-duration: 1s; 
                    -webkit-animation-fill-mode: forwards; 
                    animation-fill-mode: forwards ;
                    animation:twinkling 0.5s infinite ease-in-out;
                    background-color: red !important;
                } 
                @keyframes twinkling{ 
                    0%{ 
                        opacity: 0.1; 
                        margin-top: -3px;
                    } 
                    50%{ 
                        opacity: 1; 
                        margin-top: 0px;
                    } 
                    100%{ 
                        opacity: 0.1; 
                        margin-top: -3px;
                    } 
                }
                // background: var(--color-brand,#006cff);
                // border-radius: 3px;
                // box-shadow: 0 1px 4px 0 rgb(31 56 88 / 15%);
            }
        }
        .simulator_insertion{
            position:absolute;
            display:block;
            z-index:12;
            pointer-events:none!important;
            height:2px;
            line-height:0;
            font-size:0;
            will-change:transform
        }
        .simulator_insertion:after,
        .simulator_insertion:before{
            position:absolute;
            content:"";
            left:0;
            top:-2px;
            width:0;
            height:0;
            border-style:solid;
            border-width:3px 0 3px 5px;
        }
        .simulator_insertion:after{
            left:auto;
            right:0;
            border-width:3px 5px 3px 0;
        }
        .simulator_insertion.simulator_vertical{
            width:2px;
            height:auto
        }
        .simulator_insertion.simulator_vertical:after,
        .simulator_insertion.simulator_vertical:before{
            top:0;
            left:-2px;
            right:auto
        }
        .simulator_insertion.simulator_vertical:before{
            border-width:5px 3px 0;
        }
        .simulator_insertion.simulator_vertical:after{
            top:auto;
            bottom:0;
            border-width:0 3px 5px;
        }

    }
    .top_toolbar{
        position:absolute;
        height: 60px;
        left: 0;
        right: 0;
        top: 0;
        z-index: 110;
        transition: all linear 0.3s;
        .toolbar_inner_box{
        }
        .ctrl_btn{
            position: absolute;
            width: 40px;
            height: 20px;
            color: #ffffff;
            font-size: 12px;
            background-color: rgba(0,0,0,0.4);
            text-align: center;
            left: 50%;
            margin-left: -20px;
            bottom: 80px;
            line-height: 20px;
            user-select: none;
            cursor: pointer;
            opacity: 0;
            transition: all linear 0.3s;
            border-bottom-left-radius: 5px;
            border-bottom-right-radius: 5px;
            &:hover{
                opacity: 1;
            }
        }
    }
    //
    .lr_sidebar{
        position:absolute;
        width: 300px;
        top: 60px;
        bottom: 0px;
        z-index: 111;
        transition: all linear 0.3s;
        .ctrl_btn{
        position: absolute;
        width: 20px;
        height: 40px;
        color: #ffffff;
        font-size: 12px;
        background-color: rgba(0,0,0,0.4);
        text-align: center;
        top: 50%;
        margin-top: -20px;
        line-height: 16px;
        padding-top: 4px;
        user-select: none;
        cursor: pointer;
        opacity: 0.8;
        transition: all linear 0.3s;
        &:hover{
            opacity: 1;
        }
        }
        &.pos_left{
        left: 0;
        &.area_close{
            left: -300px;
            .ctrl_btn{
            right: -20px;
            }
        }
        .ctrl_btn{
            right: 350px;
            border-top-right-radius: 5px;
            border-bottom-right-radius: 5px;
        }
        }
        &.pos_right{
        right: 0;
        &.area_close{
            right: -300px;
            .ctrl_btn{
            left: -20px;
            }
        }
        .ctrl_btn{
            left: 350px;
            border-top-left-radius: 5px;
            border-bottom-left-radius: 5px;
        }
        }
        .ant-tabs{
        height: 100%;
        .ant-tabs-content{
            height: calc(100% - 44px);
        }
        .ant-tabs-nav .ant-tabs-tab {
            text-align: center;
            width: 150px;
            margin: 0;
        }
        &.ant-tabs-bottom .ant-tabs-bottom-bar{
            margin-top: 0px;
            border-top: none;
            transition: background linear 0.3s;
        }
        }
        .pane_content_title{
        height: 50px;
        line-height: 50px;
        padding: 0px 15px;
        font-size: 16px;
        position: relative;
        transition: background linear 0.3s;
        .fewer_btn{
            position: absolute;
            right: 0;
            top: 0;
            width: 50px;
            text-align: center;
            opacity: 0.6;
            transition: all linear 0.3s;
            cursor: pointer;
            &:hover{
            opacity: 0.8;
            }
        }
        i.anticon{
            opacity: 0.4;
            cursor: pointer;
            margin-left: 5px;
            transition: all linear 0.3s;
            &:hover{
            opacity: 0.6;
            }
        }
        }
    }
    
    //
    .mix-attribute,.data-attribute{
        .mixattr_attribute_box.pt10{
            padding-top: 10px;
        }
        .ant-collapse-icon-position-right > .ant-collapse-item > .ant-collapse-header{
            padding: 6px 15px;
            .ant-collapse-arrow{
                right: 15px;
            }
        }
        .ant-collapse-content > .ant-collapse-content-box{
            padding: 10px 15px;
        }
        .help_tip_icon{
            opacity: 0.4;
            transition: all linear 0.2s;
            &:hover{
            opacity: 0.5;
            }
        }
        .ant-collapse{
            border-top: none;
        }
        .ant-collapse-extra{
            position: absolute;
            right: 36px;
            top: 0px;
            line-height: 34px;
            .extra_op{
                i{
                    margin-left: 10px;
                }
            }
        }
        .layoutType-block,.layoutType-inline{
            margin-bottom: 10px;
        }
        .layoutType-inline{
            display: flex;
            justify-content: start;
            .label_text{
                flex-shrink: 0;
                width: 85px;
                padding-right: 5px;
                white-space: nowrap;
                text-overflow: ellipsis;
                overflow: hidden;
                line-height: 22px;
            }
            .ctrl_box{
                width: 0;
                flex-grow: 1;
            }
        }
        .label_text{
            .help_tip_icon{
                margin-left: 3px;
            }
        }
        
        .layoutType-block{
            text-align: left;
            .label_text{
                margin-bottom: 5px;
            }
            .ctrl_box{
                width: 100%;
            }
        }
        .inlineGroup{
            display: flex;
            justify-content: start;
            .layoutType-inline{
                display: flex;
                .label_text{
                    width: auto;
                    padding-right: 5px;
                    white-space: nowrap;
                    text-overflow: initial;
                    overflow: initial;
                }
                .ctrl_box{
                    width: auto;
                }
            }
            >div{
                margin-right: 10px;
                &:last-child{
                    margin-right: 0px;
                }
            }
        }
        .ctrl_box{
            .layout-icon{
                font-size: 18px;
            }
            .idm-mt5{
                margin-top: 5px;
            }
            &.uploadimg_box{
                .ant-input-affix-wrapper .ant-input-suffix{
                    right: 8px;
                }
            }
            
            .idm-svg-icon{
                font-size: 14px;
                max-height: 14px;
                width: 14px;
                fill: currentColor;
                vertical-align: middle;
                margin-top: -2px;
                margin-right: 5px;
            }
            .action_item{
                display: flex;
                justify-content: space-between;
                height: 30px;
                line-height: 30px;
                padding: 0px 10px;
                .action_item_name{
                    white-space: nowrap;
                    text-overflow: ellipsis;
                    overflow: hidden;
                }
                .handle{
                    cursor: ns-resize;
                }
                .action_icon{
                    display: flex;
                    &>*{
                        margin-left: 5px;
                        cursor: pointer;
                    }
                }
            }
        }
        .ctrl_box>*{
            display: block;
        }
        
        .ctrl_box.idm_colorpicker_input .ant-input-affix-wrapper .ant-input-prefix{
            left: 4px;
        }
        .ctrl_box.idm_colorpicker_input .ant-input-affix-wrapper.ant-input-affix-wrapper-sm input{
            padding-left: 25px;
        }
        
        .collapse_level_0 .ant-collapse-item .ant-collapse-content{
            .collapse_level_1,.collapse_level_2,.collapse_level_3{
                margin: 0px;
                border: none;
                background: transparent;
                margin-bottom: 6px;
                .ant-collapse-header{
                    padding-left: 0px;
                    padding-right: 0px;
                    // padding-top: 0px;
                    .ant-collapse-arrow{
                        right: 0;
                    }
                    .ant-collapse-extra{
                        right: 21px;
                    }
                }
            }
        }
        .ant-input-affix-wrapper .ant-input{
            padding-left: 7px;
        }
        .firstAttrContainer{
            padding: 0px 15px 0px 15px;
        }
        .islider_box,.inumberunit_box{
            display:flex;
            flex-grow: 1;
            .islider_ctrl{
                min-width:70px;
                margin-top: 6px;
                margin-bottom: 6px;
                flex-grow: 1;
            }
            .islider_ctrl_input{
                width:60px;
                margin-left:5px
            }
            .unit_select{
                width:0px;
                flex-grow: 1;
                >*{
                    display: block;
                    margin-left: -1px;
                }
            }
            .number_input{
                width:60px;
                >*{
                    display: block;
                    width: 100%;
                }
            }
        }

        .ant-switch{
            margin-top: 3px;
        }
        .table_out_box{
            margin: -10px -15px;
            .table_inner_box{
                margin: 10px 15px;
            }
            .ant-tabs-bar{
                margin-bottom: 0;
                .ant-tabs-tab {
                    width: auto;
                }
            }
        }
        //padding margin 
        .ibox_layout_padding_margin{
            min-height: 157px;
            position: relative;
            user-select: none;
            .ibox_layout_margin{
                position: absolute;
                top: 18px;
                bottom: 18px;
                left: 32px;
                right: 32px;
            }
            .ibox_layout_handle{
                position: absolute;
                display: block;
                font-size: 0;
                border: 14px solid transparent;
                box-sizing: border-box;
                opacity: .8;
                &.ibox_handle_top,&.ibox_handle_bottom{
                    height: 0;
                    left: 2px;
                    right: 2px;
                    cursor: row-resize;
                }
                &.ibox_handle_top{
                    top: 0;
                    border-top-color: rgba(0,0,0,.3);
                    border-bottom-width: 0;
                    .box-arrow-icon{
                        bottom: 1px;
                        left: 50%;
                        transform: translateX(-50%);
                    }
                }
                &.ibox_handle_bottom{
                    bottom: 0;
                    border-bottom-color: rgba(0,0,0,.3);
                    border-top-width: 0;
                    .ibox_handle_inscription{
                        position: absolute;
                        left: 3px;
                        top: -1px;
                        font-size: 12px;
                        text-transform: uppercase;
                        transform-origin: left;
                        transform: scale(.6);
                        pointer-events: none;
                    }
                    .box-arrow-icon{
                        top: 1px;
                        left: 50%;
                        transform: translateX(-50%) rotate(180deg);
                    }
                }
                &.ibox_handle_left,&.ibox_handle_right{
                    width: 0;
                    bottom: 2px;
                    top: 2px;
                    cursor: col-resize;
                }
                &.ibox_handle_left{
                    left: 0;
                    border-left-color: rgba(0,0,0,.3);
                    border-right-width: 0;
                    .box-arrow-icon{
                        right: 1px;
                        top: 50%;
                        transform: translateY(-50%) rotate(-90deg);
                    }
                }
                &.ibox_handle_right{
                    right: 0;
                    border-right-color: rgba(0,0,0,.3);
                    border-left-width: 0;
                    .box-arrow-icon{
                        left: 1px;
                        top: 50%;
                        transform: translateY(-50%) rotate(90deg)
                    }
                }
                .box-arrow-icon{
                    font-size: 11px;
                    position: absolute;
                    opacity: .6;
                    max-width: none;
                    max-height: none;
                    pointer-events: none;
                }
            }
            .ibox_inline_input{
                position: absolute;
                width: 36px;
                height: 16px;
                &.ibox_layout_input_top,&.ibox_layout_input_bottom{
                    left: 50%;
                    margin-left: -18px;
                    text-align: center;
                    .ibox_inline_input_input,.ibox_inline_input_text{
                        text-align: center;
                    }
                }
                &.ibox_layout_input_top{
                    top: -18px;
                }
                &.ibox_layout_input_bottom{
                    bottom: -18px;
                }
                &.ibox_layout_input_left,&.ibox_layout_input_right{
                    top: 50%;
                    margin-top: -8px;
                    text-align: center;
                }
                &.ibox_layout_input_left{
                    left: -38px;
                    .ibox_inline_input_input,.ibox_inline_input_text{
                        text-align: right;
                    }
                }
                &.ibox_layout_input_right{
                    right: -38px;
                    .ibox_inline_input_input,.ibox_inline_input_text{
                        text-align: left;
                    }
                }
                .ibox_inline_input_text{
                    display: block;
                    color: rgba(0,0,0,.6);
                    font-size: 12px;
                    line-height: 16px;
                    opacity: .6;
                    cursor: text;
                }
                .ibox_inline_input_input{
                    position: absolute;
                    left: 0px;
                    top: 0px;
                    color: rgba(0,0,0,.6);
                    font-size: 12px;
                    border: none;
                    background: rgba(0,0,0,.5);
                    border-radius: 3px;
                    width: 100%;
                    height: 16px;
                    line-height: 16px;
                    padding: 0 2px;
                    box-sizing: border-box;
                    outline: none;
                }
                &.ibox_highlight{
                    .ibox_inline_input_text{
                        color: #1890ff;
                        font-weight: 700;
                        opacity: 0.8;
                    }
                }
            }
            
            .ibox_layout_padding{
                position: absolute;
                top: 36px;
                bottom: 36px;
                left: 50px;
                right: 50px;
                .ibox_layout_handle{
                    &.ibox_handle_top{
                        .box-arrow-icon{
                            transform: translateX(-50%) rotate(180deg);
                        }
                    }
                    &.ibox_handle_bottom{
                        .box-arrow-icon{
                            transform: translateX(-50%);
                        }
                    }
                    &.ibox_handle_left{
                        .box-arrow-icon{
                            transform: translateY(-50%) rotate(90deg);
                        }
                    }
                    &.ibox_handle_right{
                        .box-arrow-icon{
                            transform: translateY(-50%) rotate(-90deg)
                        }
                    }
                }
                .ibox_inline_input{
                    &.ibox_layout_input_top{
                        top: 16px;
                    }
                    &.ibox_layout_input_bottom{
                        bottom: 16px;
                    }
                    &.ibox_layout_input_left{
                        left: 16px;
                        .ibox_inline_input_input,.ibox_inline_input_text{
                            text-align: left;
                        }
                    }
                    &.ibox_layout_input_right{
                        right: 16px;
                        .ibox_inline_input_input,.ibox_inline_input_text{
                            text-align: right;
                        }
                    }
                }
            }
            .box_auto_btn_box{
                width: 24px;
                height: 24px;
                padding: 2px;
                border-radius: 3px;
                line-height: 22px;
                text-align: center;
                position: absolute;
                right: 0;
                bottom: 0;
                font-size: 20px;
                cursor: pointer;
                color: rgba($color: #000000, $alpha: 0.3);
                &:hover{
                    color: rgba($color: #000000, $alpha: 0.8);
                }
                &.active{
                    background-color: rgba(#000000,0.1);
                }
            }
        }
        .ctrl_box_border,.ctrl_box_radius{
            display: flex;
            border: 1px solid rgba(121, 121, 121, 0.1);
            padding-right: 10px;
            .border_ctrl_wrap{
                width: 70px;
                flex-shrink: 0;
                display: flex;
                align-items: center;
                justify-content: space-around;
                font-size: 12px;
                .border_setting_row{
                    &>div{
                        margin: 2px;
                        padding:3px 5px;
                        border-radius: 2px;
                        text-align: center;
                        cursor: pointer;
                        color: rgba($color: #000000, $alpha: 0.6);
                        &:hover{
                            color: rgba($color: #000000, $alpha: 0.8);
                        }
                        &.active{
                            background-color: rgba(#000000,0.1);
                        }
                    }
                }
            }
            .border_ctrl_content{
                flex-grow: 1;
                padding: 10px 0px 0px 10px;
                .ctrl_box{
                    width: 0;
                }
                .inumberunit_box{
                    .number_input{
                        width: 50px;
                    }
                }
            }
            .layoutType-inline .label_text{
                width: 40px;
                text-align: right;
            }
            .inumberunit_box .unit_select{
                width: 0;
            }
        }
        .ctrl_box_border{
        }
        .ctrl_box_radius{
            margin-top: 5px;
        }
        .ctrl_box_fontbox{
            .label_text{
                width: 30px !important;
                text-align: center;
            }
            .inlineGroup{
                .layoutType-inline{
                    .ctrl_box{
                        width: 0;
                    }
                }
            }
            .inner_width_100{
                .ant-radio-group{
                    display: flex;
                    justify-content: space-between;
                    .ant-radio-button-wrapper{
                        flex-grow: 2;
                        text-align: center;
                    }
                }
            }
        }
        .ant-radio-button-wrapper {
            margin-bottom: 2px;
        }
        
        .colorpicker_icon{
            width: 16px;
            height: 16px;
            /* border-radius: 3px; */
        }
        .ant-tag{
            margin-bottom: 5px;
            background: transparent;
        }
    }
    //
    &.fullscreen-false{
        #drag_wrapper{
            margin-top: 60px;
            height: calc(100vh - 60px);
            width: calc(100vw - 600px);
            margin-left: 300px;
            .drag_workspace{
            display: inline-block;
            margin:45px 30px 30px 45px;
            }
            .drag_simulator_overlay{
                margin:45px 30px 30px 45px;
            }
        }
    }
    //
    &.fullscreen-true{
        .top_toolbar{
            top: -60px;
            &.temporary_open{
                top: 0px;
            }
            .ctrl_btn{
                opacity: 0.8;
                bottom: -20px;
                &:hover{
                opacity: 1;
                }
