.idm-quick-search,.idm-quick-search-popover{
    position: fixed;
    width: 580px;
    top: 8%;
    left: 50%;
    margin-left: -290px;
    z-index: 19920326;
    padding: 15px;
    border-radius: 3px;
    &.min-search{
        position: relative;
        width: 300px;
        top: initial;
        left: initial;
        padding:0;
        margin-left: 0px;
        .idm-quick-search-input{
            input{
                width: 100%;
                font-size: 14px;
                background: none;
                padding: 5px;
                &:focus{
                    outline: none;
                }
                &::placeholder{
                    font-size: 14px;
                }
            }
        }
        .idm-quick-search-results{
            margin-top: 10px;
        }
    }
    .idm-quick-search-input{
        input{
            width: 100%;
            padding-right: 15px;
            font-size: 20px;
            border: none;
            background: none;
            &:focus{
                outline: none;
            }
            &::placeholder{
                font-size: 20px;
            }
        }
    }
    .idm-quick-search-close{
        position: absolute;
        right: 10px;
        top: 10px;
        opacity: 0.6;
        cursor: pointer;
        &:hover{
            opacity: 1;
        }
    }
    .idm-quick-search-results{
        margin-top: 20px;
        max-height: 400px;
        overflow-y: auto;
        .idm-quick-search-result-item-wrapper{
            position: relative;
            display: flex;
            align-items: center;
            width: 100%;
            height: 48px;
            overflow: hidden;
            font-size: 12px;
            white-space: nowrap;
            border-radius: 3px;
            cursor: pointer;
            .idm-quick-search-result-item-type{
                display: inline-block;
                text-align: center;
                width: 36px;
                min-width: 36px;
                height: 36px;
                padding: 7px;
                margin-right: 10px;
                margin-left: 5px;
                border-radius: 3px;
            }
            .idm-quick-search-result-item-cbox{
                flex-grow: 2;
                padding-right: 10px;
            }
            .idm-quick-search-result-item-title-wrapper{
                display: flex;
                justify-content: space-between;
                .idm-quick-search-result-item-name{
                    font-size: 14px;
                }
                .idm-quick-search-result-item-version{
                    font-size: 12px;
                }
            }
            .idm-quick-search-result-item-content{
                font-size: 12px;
            }
            .check_layer{
                position: absolute;
                left: 0;
                top: 0;
                right: 0;
                bottom: 0;
                .check_icon{
                    position: absolute;
                    right: 20px;
                    top: 50%;
                    font-size: 20px;
                    margin-top: -10px;

                }
            }
        }
        &.idm-attr-item-box{
            display: flex;
            flex-wrap:wrap;
            .idm-attr-item-wrapper{
                position: relative;
                width: 48px;
                height: 48px;
                cursor: pointer;
                .idm-attr-item-node{
                    text-align: center;
                    .idm-svg-icon{
                        font-size: 26px;
                        max-height: 26px;
                        width: 26px;
                        margin-top: 11px;
                        fill: currentColor;
                    }
                }
                .check_layer{
                    position: absolute;
                    left: 0;
                    top: 0;
                    right: 0;
                    bottom: 0;
                    border-radius: 5px;
                    .check_icon{
                        position: absolute;
                        right: 1px;
                        bottom: 0px;
                        font-size: 12px;
                    }
                }
            }
        }
    }
    
    .idm-svg-icon{
        font-size: 22px;
        max-height: 22px;
        width: 22px;
        margin-top: 0px;
        fill: currentColor;
    }
}
