@import "@/style/common/common.scss";


























































































































































































































































































































































































































































































































































































































































































































































































































































































































































































.mix-attribute{
    height: calc(100% - 50px);
    overflow: auto;
    position: relative;
    .mixattr_baseinfo{
      padding:10px 15px;
      position: relative;
      .mixattr_baseinfo_version{
        display: flex;
        align-items: center;
        font-size: 12px;
      }
      .help_icon{
        position: absolute;
        right: 15px;
        top: 50%;
        margin-top: -9px;
        font-size: 18px;
        opacity: 0.4;
        transition: all linear 0.2s;
        cursor: pointer;
        &:hover{
        opacity: 0.5;
        }
      }
    }
    .ant-divider-horizontal{
        margin: 0px;
    }
}
