@import "@/style/common/common.scss";


































































































































































































































































































































.page-component{
    height: calc(100% - 50px);
    overflow: hidden;
    position: relative;
    .pagecom_navigator{
      .pagecom_navigator_inner{
        height: 43px;
        position: relative;
        margin-right: 44px;
        white-space: nowrap;
        .list_type_box{
          position: absolute;
          right: -38px;
          top: 6px;
          .ant-btn{
            border: none !important;
          }
          .bicon{
            font-size: 16px;
          }
        }
        >div{
          display: flex;
          line-height: 43px;
          max-width: 100%;
          overflow: hidden;
          >div{
            margin-left: 5px;
            font-size: 12px;
          }
        }
        .nav_name{
          cursor: pointer;
        }
      }
      .ant-divider-horizontal{
          margin: 0px;
      }
    }
    .pagecom_content{
        height: calc(100% - 44px);
        overflow-x: hidden;
        overflow-y: auto;
    }
    .ant-divider-horizontal{
        margin: 0px;
    }
    .ant-collapse-content-box .ant-divider-horizontal:last-child{
      display: none;
    }
}
